@use "../../styles/mixins";
@use "../../styles/variables";

.tool {
    text-align: center;
    @include mixins.text-style(variables.$color-content, 1.125rem, 400);

    @media (max-width: variables.$breakpoint-medium) {
        transform: scale(95%);
    }

    @media (max-width: variables.$breakpoint-small) {
        font-size: 1rem;
        transform: scale(85%);
    }

    &--mern {
        transform: scale(110%);

        @media (max-width: variables.$breakpoint-medium) {
            transform: scale(100%);
        }

        @media (max-width: variables.$breakpoint-small) {
            transform: scale(90%);
        }
    }

    &--column {
        transform: scale(90%);

        @media (max-width: variables.$breakpoint-small) {
            transform: scale(85%);
        }
    }

    &__logos {
        display: flex;
        justify-content: center;
        gap: 20px;
        margin-bottom: 20px;

        @media (max-width: variables.$breakpoint-small) {
            gap: 15px;
            margin-bottom: 15px;
        }
    }

    &__logo {
        fill: currentColor;
        height: 54px;

        @media (max-width: variables.$breakpoint-medium) {
            height: 52px;
        }

        @media (max-width: variables.$breakpoint-small) {
            height: 48px;
        }
    }
}
